@import '../webix_widget/fonts/font-awesome.css';
@import '../webix_widget/fonts/font-Roboto.css';
.webix_view {
  font-family: Roboto, Arial;
  font-size: 13px;
  color: #545454;
  -webkit-font-smoothing: antialiased;
  cursor: default;
  overflow: hidden;
  border: 0px solid #f6f6f6;
  background-color: #f6f6f6;
  white-space: normal;
  -webkit-appearance: none;
}
.webix_view.webix_form {
  background-color: #f6f6f6;
  /*.webix_view{
		background-color: @backColor;
	}*/
}
body {
  -webkit-tap-highlight-color: transparent !important;
}
/*
	webix_layout_{type}
	known types are: line, clean, wide, space;
*/
.webix_abslayout {
  position: relative;
}
.webix_abslayout > .webix_view {
  position: absolute;
}
.webix_layout_space {
  background-color: #f6f6f6;
}
.webix_layout_wide {
  background-color: #f6f6f6;
}
.webix_layout_line,
.webix_multiview,
.webix_layout_clean,
.webix_layout_accordion {
  background: transparent;
}
.webix_overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 10;
  text-align: center;
  padding-top: 10px;
}
.webix_view > .webix_disabled {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ddd;
  opacity: 0.4;
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=40);
  z-index: 1;
}
.webix_disabled_view {
  overflow: hidden !important;
  position: relative;
}
body.webix_full_screen {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.webix_noselect,
.webix_noselect div {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.webix_selectable {
  -moz-user-select: text;
  -webkit-user-select: text;
  user-select: text;
}
.webix_template {
  height: 100%;
  padding: 10px;
  padding-left: 10px 2px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.webix_clean {
  background: transparent;
}
.webix_clean .webix_template {
  padding: 0px;
}
.webix_section {
  padding-left: 30px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: transparent;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAAUCAIAAADDbMD2AAAABnRSTlMA/wD/AP83WBt9AAAACXBIWXMAAA7EAAAOxAGVKw4bAAAAHElEQVR4nGP4//8/EwMDA158/vx5BmLUMTAwAADGhQiMQPqPwgAAAABJRU5ErkJggg==);
  background-repeat: repeat-x;
  background-position: 6.5px;
  color: #545454;
  font-size: 11.7px;
  text-transform: uppercase;
  line-height: 26px;
}
.webix_section > .webix_template {
  padding: 0px 8px;
  background-color: #f6f6f6;
  display: inline;
}
.webix_layout_space > .webix_section > .webix_template {
  padding: 0px 8px;
  background-color: #f6f6f6;
}
.webix_layout_wide > .webix_section > .webix_template {
  padding: 0px 8px;
  background-color: #f6f6f6;
}
.webix_layout_line > .webix_section > .webix_template,
.webix_layout_clean > .webix_section > .webix_template {
  padding: 0px 8px;
  background-color: #f6f6f6;
}
.webix_header > div {
  padding-left: 10px;
  font-family: Roboto, Arial;
  font-size: 13px;
  color: #545454;
  font-size: 14px;
  font-weight: bold;
  background: #f6f6f6;
  line-height: 24px;
  line-height: 26px;
  padding-top: 0;
  padding-bottom: 0;
}
.webix_spacer {
  background: transparent;
}
.webix_view_animate {
  position: relative;
  width: 100%;
  height: 100%;
}
x\:ui {
  display: none;
}
.webix_view.webix_flexlayout {
  display: flex !important;
  flex-wrap: wrap;
  flex-direction: row;
  overflow: hidden;
  box-sizing: border-box;
}
.webix_flexlayout > div {
  display: block !important;
}
.webix_view_align {
  background: transparent;
}
/*Focusing*/
.webix_view div,
.webix_view span,
.webix_view a.webix_list_item {
  outline: none;
}
.webix_view div:focus,
.webix_view span:focus,
.webix_view a.webix_list_item:focus,
.webix_view.webix_window:focus {
  outline-color: #adadad;
  outline-style: dotted;
  outline-width: 1px;
}
textarea,
input,
select,
button,
.webix_view div.webix_inp_static:focus {
  outline: none;
}
.webix_resize_area {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.webix_resize_handle_x .webix_handle_content,
.webix_resize_handle_y .webix_handle_content {
  width: 100%;
  height: 100%;
  background: rgba(100, 100, 100, 0.1);
  border: 1px dotted #ffffff;
}
.webix_resize_handle_x {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  cursor: ew-resize;
  width: 1px;
  height: 100%;
  position: absolute;
  z-index: 2;
  top: 0px;
}
.webix_resize_handle_x .webix_handle_content {
  border-width: 0px 1px;
}
.webix_resize_origin_x {
  cursor: ew-resize;
  width: 3px;
  height: 100%;
  position: absolute;
  z-index: 2;
  top: 0px;
}
.webix_resize_handle_y {
  cursor: ns-resize;
  height: 1px;
  width: 100%;
  position: absolute;
  z-index: 2;
  left: 0px;
}
.webix_resize_handle_y .webix_handle_content {
  border-width: 1px 0px;
}
.webix_resize_origin_y {
  cursor: ns-resize;
  height: 3px;
  width: 100%;
  position: absolute;
  z-index: 2;
  left: 0px;
}
.webix_resizer.webix_disabled_view {
  cursor: default;
}
.webix_resizer_vy {
  cursor: ns-resize;
  margin-top: 0px !important;
  border: 0px !important;
  padding: 0px !important;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABcAAAADAQMAAABCowZuAAAABlBMVEUAAACkvtSA7tmIAAAAAXRSTlMAQObYZgAAAA9JREFUCB1jXMUABoxAGgAJaAFXPIkJqAAAAABJRU5ErkJggg==) no-repeat center center;
}
.webix_resizer_vx {
  cursor: ew-resize;
  margin-left: 0px !important;
  border: 0px !important;
  padding: 0px !important;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAAXAQMAAAD0oWdxAAAABlBMVEUAAACkvtSA7tmIAAAAAXRSTlMAQObYZgAAAA5JREFUeF5jWMDAQDwGAKyuB4FpGQdzAAAAAElFTkSuQmCC) no-repeat center center;
}
.webix_resizer_y {
  cursor: ns-resize;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABcAAAADAQMAAABCowZuAAAABlBMVEUAAACkvtSA7tmIAAAAAXRSTlMAQObYZgAAAA9JREFUCB1jXMUABoxAGgAJaAFXPIkJqAAAAABJRU5ErkJggg==) no-repeat center center;
  background-color: #ffffff;
}
.webix_resizer_x {
  cursor: ew-resize;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAAXAQMAAAD0oWdxAAAABlBMVEUAAACkvtSA7tmIAAAAAXRSTlMAQObYZgAAAA5JREFUeF5jWMDAQDwGAKyuB4FpGQdzAAAAAElFTkSuQmCC) no-repeat center center;
  background-color: #ffffff;
}
.webix_accordionitem_body {
  position: relative;
}
.webix_accordionitem_header {
  border-bottom: 1px solid #f6f6f6;
  position: relative;
  z-index: 2;
  font-family: Roboto, Arial;
  font-size: 13px;
  color: #545454;
  font-size: 14px;
  font-weight: bold;
  background: #f6f6f6;
  line-height: 24px;
  cursor: pointer;
  overflow: hidden;
}
/* IE8 */
.webix_ie.horizontal > .webix_accordionitem_header.collapsed {
  -ms-writing-mode: tb-lr;
}
.webix_ie.horizontal > .webix_accordionitem_header.collapsed .webix_accordionitem_label {
  padding-left: 0;
  padding-top: 10px;
}
.webix_ie.horizontal > .webix_accordionitem_header.collapsed .webix_accordionitem_button {
  margin: 0;
}
.webix_accordionitem_label {
  height: 100%;
  padding-left: 10px;
}
.webix_accordionitem_button {
  float: right;
  position: relative;
  height: 11px;
  width: 11px;
  margin: 10px 10px;
}
