.webix_carousel {
  position: relative;
}
.webix_nav_panel {
  bottom: 15px;
  height: auto;
  line-height: 8px;
  overflow: hidden;
  position: absolute;
  text-align: center;
}
.webix_nav_item {
  display: inline-block;
  height: 8px;
  padding: 6px 5px 6px 4px;
  width: 8px;
  cursor: pointer;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.webix_nav_item div {
  border-radius: 50%;
  display: block;
  height: 8px;
  width: 8px;
}
.webix_nav_inactive div {
  border: 1px solid rgba(0, 0, 0, 0.15);
  filter: alpha(opacity=60);
  opacity: 0.6;
  background: #fff;
}
.webix_nav_active div {
  border: 1px solid rgba(0, 0, 0, 0.1);
  background: #fff;
}
.webix_nav_button_prev,
.webix_nav_button_next {
  cursor: pointer;
  display: block;
  height: 24px;
  position: absolute;
  width: 24px;
}
.webix_nav_button_prev .webix_nav_button_inner,
.webix_nav_button_next .webix_nav_button_inner {
  height: 100%;
  width: 100%;
}
