.webix_nav_button_corner.webix_nav_button_next .webix_nav_button_inner:hover {
  opacity: 1;
}
.webix_nav_panel_side {
  margin: 0 auto;
  left: 0;
  width: 100%;
}
.webix_nav_button_side {
  height: 34px;
