.webix_nav_button_side.webix_nav_button_prev .webix_nav_button_inner {
  opacity: 0.8;
  background-image: url(data:image/png;base64,);
}
.webix_nav_button_side.webix_nav_button_prev .webix_nav_button_inner:hover {
