.webix_list_item {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 2px 10px;
  line-height: 15px;
  cursor: pointer;
  border-bottom: 1px solid #f6f6f6;
  text-align: left;
}
.webix_list_item.webix_invalid {
  background-color: #ffeeee;
}
.webix_list_item.webix_selected {
  color: #f6f6f6;
  background: #f2b9d0;
}
.webix_list .webix_list_item.webix_selected {
  border-bottom: 1px solid #f6f6f6;
  padding: 2px 10px;
}
.webix_group {
  position: relative;
}
.webix_group_back,
.webix_group_back.webix_selected {
  position: relative;
  background: #ababab;
  color: #323232;
}
.webix_arrow_icon {
  position: absolute;
  top: 50%;
  bottom: 50%;
  margin-top: -12px;
  right: 15px;
  width: 9px;
  height: 22px;
  line-height: 22px;
  color: #545454;
  font-size: 18px;
  font-family: FontAwesome;
}
.webix_arrow_icon:before {
  content: '\f105';
}
.webix_group_back {
  padding-left: 29px;
}
.webix_group_back .webix_arrow_icon {
  left: 11px;
}
.webix_group_back .webix_arrow_icon:before {
  content: '\f104';
}
.dhx_pagelist {
  position: relative;
}
.webix_list_count {
  float: right;
  width: 25px;
  font-size: 14px;
  text-align: center;
  margin-right: 45px;
  box-shadow: 0 1px 0 #000000;
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.6);
}
.webix_unit_header {
  padding: 0 10px;
  line-height: 19px;
  text-align: left;
  background-color: #ababab;
}
.webix_list-x .webix_list_item {
  display: inline-block;
  vertical-align: top;
  border-right: 1px solid #f6f6f6;
  border-bottom: none;
}
.webix_list-x .webix_list_item.webix_selected {
  border-left: 1px solid #f6f6f6;
  border-right: 1px solid #f2b9d0;
  padding-left: 9px;
}
.webix_list-x .webix_scroll_cont {
  display: inline;
}
.webix_view.webix_list-x {
  white-space: nowrap;
}
/* Style for disabled items */
.webix_list_item.webix_disabled {
  color: #8b949e;
}
.webix_list_item.webix_disabled .webix_submenu_icon {
  visibility: hidden;
}
/*button of pager*/
.webix_view.webix_pager {
  padding: 2px 0;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: transparent;
}
.webix_pager_item,
.webix_pager_item_selected {
  padding: 0 6px;
  text-align: center;
  height: 100%;
  width: auto;
  min-width: 32px;
  border: 1px solid #f6f6f6;
  margin: 0 2px;
  cursor: pointer;
  background-color: white;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: Roboto, Arial;
  font-size: 13px;
}
/*selected  button of pager*/
.webix_pager_item_selected {
  cursor: default;
  background-color: #f2b9d0;
  border: 1px solid #f6f6f6;
}
.webix_animation .webix_ss_header {
  position: relative;
  z-index: 10;
}
.webix_animation .webix_ss_footer,
.webix_animation .webix_vscroll_x {
  position: absolute;
  bottom: 0px;
  z-index: 10;
}
/*helper for allowing selection*/
.webix_allow_selection {
  -moz-user-select: text;
  -webkit-user-select: text;
  user-select: text;
}
/*default item in dataview*/
.webix_dataview_item {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 2px 10px;
  line-height: 15px;
  cursor: pointer;
  border-bottom: 1px solid #f6f6f6;
  text-align: left;
  border-right: 1px solid #f6f6f6;
}
/*default item in selected state*/
.webix_dataview_item.webix_selected {
  color: #f6f6f6;
  background: #f2b9d0;
  border-bottom: 1px solid #f6f6f6;
  border-right: 1px solid #f6f6f6;
}
.webix_dataview_item.webix_invalid {
  background-color: #ffeeee;
}
/*chart container*/
.webix_chart {
  position: relative;
  font-family: Roboto, Arial;
  font-size: 13px;
  color: #545454;
  overflow: hidden;
  z-index: 1;
}
.webix_chart canvas {
  position: absolute;
}
/*labels*/
.webix_canvas_text {
  position: absolute;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  font-size: 12px;
  z-index: 100;
}
/*map*/
.webix_map_img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  border: 0px;
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
  opacity: 0;
}
/*scales*/
.webix_axis_item_y {
  position: absolute;
  height: 18px;
  line-height: 18px;
  /*line-height:10px;*/
  margin-top: -4px;
  text-align: right;
}
.webix_axis_item_x {
  padding-top: 2px;
}
.webix_axis_title_x {
  text-align: center;
  font-size: 13px;
}
.webix_axis_title_y {
  text-align: center;
  font-family: Verdana;
  font-size: 13px;
  /*safari*/
  -webkit-transform: rotate(-90deg);
  /*firefox*/
  -moz-transform: rotate(-90deg);
  /*opera*/
  -o-transform: rotate(-90deg);
  /*IE9*/
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
  padding-left: 3px;
}
.webix_ie_filter {
  /*IE8*/
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
}
/*legend block*/
.webix_chart_legend {
  position: absolute;
  z-index: 1000;
}
.webix_chart_legend_item {
  height: 18px;
  line-height: 18px;
  padding: 2px;
}
.webix_chart_legend_item.hidden {
  color: #d3d3d3;
}
.webix_axis_item_y.webix_radar {
  color: #555;
  font-size: 11px;
  padding-right: 3px;
  height: 13px;
  line-height: 13px;
  margin-top: 0px;
}
.webix_canvas_text.webix_axis_radar_title {
  margin-top: 0px;
  padding-top: 0px;
  color: #878787;
  font-size: 12px;
}
.webix_axis_item_y,
.webix_axis_item_x {
