.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
.webix_icon,
.webix_input_icon {
  font-size: 17px;
  display: inline-block;
  width: 20px;
}
.webix_input_icon {
  float: right;
  font-size: 19px;
  color: #888;
}
.webix_icon_button {
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  position: relative;
}
.webix_icon_button .webix_icon {
  font-size: 20px;
  height: 23px;
  width: 21px;
  display: inline-block;
  text-align: center;
  color: #545454;
}
.webix_icon_btn {
  font-size: 17px;
  display: inline-block;
  text-align: center;
  width: 24px;
  color: #545454;
}
.webix_el_iconBbutton .webix_icon_btn {
  text-align: center;
  width: 100%;
  padding: 5px 0px;
}
.webix_el_icon {
  cursor: pointer;
}
.webix_badge {
  background-color: #ff8839;
  color: #f6f6f6;
  border-radius: 50%;
  font-size: 12px;
  height: 22px;
  min-width: 22px;
  box-sizing: border-box;
  padding: 0 4px;
  text-align: center;
  line-height: 21px;
  position: absolute;
  right: 0;
  top: 0;
}
.webix_list_item .webix_badge {
  position: static;
  float: right;
  margin: 4px -5px 0 10px;
}
.webixtype_base .webix_badge {
  top: 50%;
  margin-top: -11px;
  margin-right: 8px;
}
.webix_treemap {
  background-color: #f5f5f5;
}
.webix_treemap .webix_scroll_cont {
  position: relative;
  height: 100%;
  width: 100%;
}
.webix_treemap_item {
  position: absolute;
  color: #444;
  overflow: hidden;
  text-align: left;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 3px 5px;
  border-width: 0px 1px 1px 0;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.2);
  background: transparent;
  cursor: pointer;
}
.webix_treemap_level_top {
  z-index: 1;
  border-color: rgba(0, 0, 0, 0.4);
}
.webix_treemap_item_bottom {
  border-bottom-width: 0;
}
.webix_treemap_item_right {
  border-right-width: 0;
}
.webix_treemap_item:hover {
  box-shadow: inset 0 0 5px #666;
}
.webix_treemap_item.webix_selected {
  border-color: #f2b9d0;
  box-shadow: inset 0 0 1px 2px #f2b9d0;
}
.webix_treemap_header {
  width: 100%;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.webix_treemap_header {
  padding: 0 10px;
  border-bottom: 1px solid #f6f6f6;
}
.webix_treemap_header_item {
  cursor: pointer;
}
.webix_treemap_header_item:last-child {
  cursor: default;
}
.webix_treemap_reset {
  float: right;
  width: 25px;
  text-align: center;
}
.webix_treemap_reset:before {
  content: "\f00d";
  color: #545454;
  font-family: FontAwesome;
  font-size: 16px;
  display: block;
  cursor: pointer;
}
.webix_treemap_path_icon {
  width: 20px;
  text-align: center;
}
.webix_barcode {
  position: relative;
}
.webix_barcode .webix_canvas_text {
  font-size: 13px;
  padding: 0 2px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.portlet_drag {
  position: absolute;
  z-index: 1;
  top: 5px;
  right: 5px;
  width: 18px;
  opacity: 0.5;
  cursor: pointer;
}
.portlet_in_drag {
  opacity: 0.4;
}
.portlet_marker,
.portlet_markertop,
.portlet_markerbottom,
.portlet_markerleft,
.portlet_markerright {
  opacity: 0.5;
  background: #9169BE;
  position: absolute;
  z-index: 2;
  transition: top 0.5s, left 0.5s, width 0.5s, height 0.5s;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.portlet_markertop {
  height: 50%;
}
.portlet_markerbottom {
  height: 50%;
  top: 50%;
}
.portlet_markerleft {
  width: 50%;
}
.portlet_markerright {
  width: 50%;
  left: 50%;
}
.panel_icon {
  position: absolute;
  z-index: 100;
  top: 5px;
  right: 5px;
  line-height: normal;
  font-size: 13px;
  width: 18px;
  opacity: 0.5;
  cursor: pointer;
}
.panel_target {
  position: absolute;
  background: rgba(120, 120, 120, 0.84);
  box-shadow: 0px 0px 10px 5px #666;
  z-index: 3;
}
.webix_popup.webix_sidemenu.webix_sidemenu_left {
  border-width: 0 1px 0 0;
  box-shadow: 2px 0 2px rgba(0, 0, 0, 0.05);
}
.webix_sidemenu_left.webix_animate {
  transition: left 0.6s;
}
.webix_popup.webix_sidemenu.webix_sidemenu_right {
  left: auto;
  border-width: 0 0 0 1px;
  box-shadow: -2px 0px 2px rgba(0, 0, 0, 0.05);
}
.webix_sidemenu_right.webix_animate {
  transition: right 0.6s;
}
.webix_popup.webix_sidemenu.webix_sidemenu_top {
  border-width: 0 0 1px 0 ;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.05);
}
.webix_sidemenu_top .webix_win_body {
  position: relative;
}
.webix_sidemenu_top .webix_win_body > .webix_view {
  position: absolute;
  top: auto;
  left: 0;
  bottom: 0;
}
.webix_sidemenu_top.webix_animate,
.webix_sidemenu_top.webix_animate .webix_win_body {
  transition: height 0.6s;
}
.webix_popup.webix_sidemenu.webix_sidemenu_bottom {
  border-width: 1px 0 0 0 ;
  top: auto;
  box-shadow: 0 -2px 2px rgba(0, 0, 0, 0.05);
}
.webix_sidemenu_bottom.webix_animate,
.webix_sidemenu_bottom.webix_animate .webix_win_body {
  transition: height 0.6s;
}
