  height: 80px;
  animation: none;
  -webkit-animation: none;
  margin-left: -40px;
  margin-top: -40px;
}
.webix_progress_icon .webix_progress_state:before {
  content: '';
  width: 80px;
  height: 80px;
  display: block;
  fill: #f6f6f6;
  color: #f6f6f6;
  background-image: url(data:image/svg+xml;base64,);
}
.webix_progress_bottom .webix_progress_state,
.webix_progress_top .webix_progress_state {
  background: #27AE60;
}
.webix_skin_mark {
  height: 220px;
}
.webix_sparklines {
  padding: 1px 0;
  box-sizing: border-box;
}
.webix_sparklines svg {
  box-sizing: border-box;
}
.webix_sparklines_line {
  stroke: #3498db;
  stroke-width: 1;
  fill: transparent;
  stroke-linecap: round;
}
.webix_sparklines_item {
  fill: #3498db;
  stroke-width: 1;
}
.webix_sparklines_bar {
  fill: #6dbcf0;
}
.webix_sparklines_bar_negative {
  fill: #d86c79;
}
.webix_sparklines_area {
  fill: #e5eef4;
}
.webix_sparklines_origin {
  stroke: #888;
  stroke-width: 1;
}
.webix_sparklines_event_area {
  fill: transparent;
}
.webix_sparklines_line_chart .webix_sparklines_event_area:hover,
.webix_sparklines_area_chart .webix_sparklines_event_area:hover,
.webix_sparklines_splinearea_chart .webix_sparklines_event_area:hover {
  fill: rgba(220, 220, 220, 0.4);
  stroke: rgba(255, 255, 255, 0.6);
}
.webix_sparklines_pie_chart .webix_sparklines_event_area:hover,
.webix_sparklines_bar_chart .webix_sparklines_event_area:hover {
  fill: rgba(255, 255, 255, 0.3);
}
/* print css rules*/
.webix_ui_print {
  display: none;
}
body.webix_print {
  margin-top: 0px;
}
@media print {
  body.webix_print {
    overflow: visible !important;
    background-color: none !important;
  }
  body.webix_print > * {
    display: none;
  }
  body.webix_print * {
    visibility: hidden;
  }
  .webix_ui_print {
    display: block !important;
    margin: 0 0 0 0!important;
    visibility: visible !important;
  }
  .webix_ui_print * {
    visibility: visible !important;
  }
  .webix_print_noscroll {
    height: auto !important;
    width: auto !important;
  }
  .webix_print_noscroll,
  .webix_print_noscroll .webix_scroll_cont {
    overflow: visible !important;
  }
  .webix_print_pagebreak {
    page-break-after: always;
  }
  .webix_print_header,
  .webix_print_footer {
    display: block !important;
    margin: 20px 0px;
    text-align: center;
    height: auto !important;
    visibility: visible !important;
  }
  .webix_print_header *,
  .webix_print_footer * {
    visibility: visible !important;
  }
  /*datatable specific*/
  .webix_table_print {
    display: table;
    visibility: visible !important;
    table-layout: fixed;
    width: 100%;
    border: 1px solid #ebebeb;
    margin-bottom: 20px;
    position: initial;
  }
  .webix_table_print td {
    overflow: hidden;
  }
  .webix_table_print tr {
    page-break-inside: avoid;
  }
  .webix_table_print .webix_cell {
    display: table-cell !important;
    border-right: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    padding: 0px 10px;
    white-space: nowrap;
  }
  .webix_table_print .webix_header_cell {
    display: table-cell;
    color: #323232;
    background: #ababab;
    border-right: 1px solid transparent;
    border-bottom: 1px solid transparent;
  }
  .webix_table_print .webix_footer_cell {
    display: table-cell;
    background: #f1f1f1;
    border-right: 1px solid transparent;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
  }
  .webix_table_print .webix_cell.webix_dtable_span {
    position: initial !important;
    white-space: normal;
  }
  .webix_table_print .webix_rotate {
    transform-origin: center 40% !important;
    -webkit-transform-origin: center 40% !important;
  }
  .webix_table_print.borderless,
  .webix_table_print.borderless .webix_cell {
    border: none;
  }
  /* x-list specific*/
  .webix_table_print.webix_view.webix_list-x {
    white-space: normal;
  }
  .webix_table_print.webix_view.webix_list-x .webix_list_item {
    display: table-cell;
  }
}
/*predefined styles for text lables inside a item*/
.webix_strong {
  font-weight: bold;
  white-space: nowrap;
}
.webix_light {
  color: #ffffff;
}
/*debug message styles*/
.webix_debug div {
  border: 1px solid orange;
  background-color: #FF9;
  color: #000;
}
.webix_debug {
  background-color: #ff9;
  border: 1px solid #ff9;
}
/*contr-styles for bootstrap*/
.webix_view,
.webix_input_icon {
  box-sizing: content-box;
}
.webix_view.webix_el_label {
  box-sizing: border-box;
}
.webix_inp_label,
.webix_inp_top_label,
.webix_label_right {
  margin-bottom: 0px;
}
.webix_view table {
  border-collapse: initial;
}
/* Dark theme: colro: light background-color: dark*/
.main_content {
  height: calc(100vh - 60px);
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  width: calc(100vw - 24px);
  margin-right: 24px;
  background-color: #f6f6f6;
  color: #545454;
  font-family: Roboto, Arial;
  font-size: 13px;
}
.content_block {
  display: flex;
  flex: 1;
  flex-direction: row;
  width: 100%;
}
/* controls, plot and optional thematic map*/
.content_left {
  font-size: 13px;
  flex: 0 0 auto;
  width: 200px;
  margin-right: 16px;
  height: calc(96vh - 60px);
  position: relative;
  display: inline-block;
  left: 6px;
  resize: horizontal;
}
.content_center {
  flex: 1 1 75%;
  min-width: 200px;
  /* padding:2px; */
}
.content_right {
  flex: 0 0 auto;
  width: 150px;
  font-size: 13px;
  height: calc(96vh - 60px);
  resize: horizontal;
  display: none;
}
.content_splitter {
  flex: 0 0 auto;
  /* no grow no shrink*/
  width: 10px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAAXAQMAAAD0oWdxAAAABlBMVEUAAACkvtSA7tmIAAAAAXRSTlMAQObYZgAAAA5JREFUeF5jWMDAQDwGAKyuB4FpGQdzAAAAAElFTkSuQmCC) no-repeat center center #f6f6f6;
  background-color: #ffffff;
  cursor: col-resize;
  margin-left: 6px;
  margin-right: 6px;
  /* z-index is a workaround for an overlap with the surrounding area making hover selection difficult */
  z-index: 10;
}
.content_splitter_horiz {
  flex: 0 0 auto;
  /* no grow no shrink*/
  height: 10px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAAXAQMAAAD0oWdxAAAABlBMVEUAAACkvtSA7tmIAAAAAXRSTlMAQObYZgAAAA5JREFUeF5jWMDAQDwGAKyuB4FpGQdzAAAAAElFTkSuQmCC) no-repeat center center #f6f6f6;
  background-color: #ffffff;
  cursor: row-resize;
  margin-top: 6px;
  margin-bottom: 6px;
  /* z-index is a workaround for an overlap with the surrounding  area making hover selection difficult */
  z-index: 10;
}
.panel-container-vertical {
  display: flex;
  flex-direction: column;
  height: 500px;
}
.widget-panel-bottom {
  flex: 1 1 auto;
}
.row {
  text-align: center;
}
.pad_plot {
  display: inline-block;
  width: 50%;
}
.plot_title {
  color: #545454;
  font-size: 14px !important;
}
.tooltip-inner {
  min-width: 200px;
  max-width: 350px;
  text-align: left;
  font-size: 14px;
}
.tooltip.top-left .tooltip-arrow {
  bottom: -5px;
  left: 5px;
  border-width: 5px 5px 0;
  border-top-color: black;
}
.tooltip.top-right .tooltip-arrow {
  bottom: -5px;
  right: 5px;
  border-width: 5px 5px 0;
  border-top-color: black;
}
.tooltip.bottom-left .tooltip-arrow {
  top: -5px;
  left: 5px;
  border-width: 0 5px 5px;
  border-bottom-color: black;
}
.tooltip.bottom-right .tooltip-arrow {
  top: -5px;
  right: 5px;
  border-width: 0 5px 5px;
  border-bottom-color: black;
}
.loader-bg {
  border: 1px solid rgba(246, 246, 246, 0.1);
  border-radius: 10px;
  box-sizing: border-box;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
@keyframes boxair {
  0% {
    box-shadow: 0 0 10px #fff, 0 0 20px #3FB3DA;
  }
  50% {
    box-shadow: none;
  }
  100% {
    box-shadow: 0 0 10px #fff, 0 0 20px #3FB3DA;
  }
}
.boxair {
  animation: boxair 2s linear 0s normal none infinite running;
}
.loadingOverlay {
  left: 35%;
  margin: auto;
  position: fixed;
  text-align: center;
  top: 30%;
  /*width: 20%;*/
  z-index: 899;
  padding: 20px;
  color: #545454;
}
select {
  color: #545454;
  background: #ababab;
}
body {
  background-color: #f6f6f6;
  font-family: Roboto, Arial;
  font-size: 13px;
  min-height: 100%;
}
textarea {
  color: #545454;
  background: #ababab;
}
#front_screen {
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 100;
  background: black;
  margin-bottom: 160px;
}
.visible_strip_footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 1;
  /* Set the fixed height of the footer here */
  height: 160px;
  background-color: black;
  color: #aaaaaa;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
}
.hidden_strip_footer {
  display: none;
  position: relative;
  height: 0%;
  width: 0%;
}
img.selected_thumbnail {
  height: 135px;
  width: auto;
  border: 4px solid #fa003c;
}
img.unselected_thumbnail {
  height: 135px;
  width: auto;
  border: 1px solid #000000;
}



